import React,{useState,useEffect} from 'react';

const About = ()=>{
  const [text,setText] = useState([]);
  useEffect(()=>{
    const url ='https://spreadsheets.google.com/feeds/list/1EYJ9WPjqKqe26InIhdzyFKZzBKgAKRkM04w7aaFQhxk/od6/public/values?alt=json';
    fetch(url) 
    .then(res => res.json())
    .then(data=>data.feed.entry)
    .then(entry=>{
      let items = [];
      entry.forEach(e=>{
        let item = e['gsx$about']['$t'];
        items.push(item);
      })
      if(items.length>=5) setText(items);
    });
  },[]);
  const aboutItems = text.map(t=>{return <>{t}<br/></>});
  return(
    <div class='content f'>
    <h3>About</h3>
    <p class='text'>
    {aboutItems}
    </p>
    </div>
  );
}

export default About;